:root {
  --color-golden-yellow: #eba421;
  --color-burnt-browm: #9a6e0e;
  --color-polish-brown: #381f00;
  --color-dark-brown: #240c00;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*, :before, :after {
  box-sizing: inherit;
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

body {
  padding: inherit;
  margin: inherit;
  color: #121212;
  background-color: #fff;
  font-family: Open sans, sans-serif;
}

section {
  padding: 4rem 0 0;
}

.hidden {
  display: none;
}

.main__header h2 {
  color: var(--color-polish-brown);
  font-size: 2.4rem;
}

.line {
  background-color: var(--color-golden-yellow);
  width: 15rem;
  height: .6rem;
}

.main__btn {
  background-color: var(--color-golden-yellow);
  color: #fff;
  text-align: center;
  border-radius: .8rem;
  width: 8rem;
  margin-top: 1.6rem;
  padding: .4rem .6rem;
  font-size: 1rem;
}

.navbar {
  background-color: var(--color-dark-brown);
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.6rem;
  display: flex;
}

.logo img {
  width: 6rem;
}

.list {
  background-color: var(--color-dark-brown);
  visibility: hidden;
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.list-link {
  text-align: center;
  color: #fff;
  border-bottom: 1px solid #fff;
  width: 20rem;
  padding: 1rem;
  font-size: 1rem;
  transition: all .5s;
  display: block;
}

.list-link:hover {
  color: var(--color-golden-yellow);
}

.current {
  color: var(--color-golden-yellow) !important;
}

.active {
  visibility: visible;
  opacity: 1;
  height: 100vh;
}

.menu {
  background-color: var(--color-burnt-browm);
  color: #fff;
  cursor: pointer;
  border-radius: .8rem;
  padding: .6rem .8rem;
  font-size: 1.4rem;
  position: relative;
}

.close {
  z-index: 999;
  display: none;
  position: fixed;
}

.show {
  display: block;
}

.hero {
  background-color: var(--color-polish-brown);
  text-align: center;
  background-image: url("deky-logo-white.44f686b1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 16rem;
}

.content {
  flex-wrap: wrap;
  display: flex;
}

.main__header {
  text-align: center;
  flex-direction: column;
  flex: 100%;
  align-items: center;
  display: flex;
}

.main__text {
  flex-direction: column;
  flex: 100%;
  padding: 2.4rem;
  line-height: 2;
  display: flex;
}

.main__text p {
  color: #121212;
  font-size: 1rem;
}

.main__color {
  background-color: var(--color-burnt-browm);
  padding-bottom: .8rem;
}

.main__color h2 {
  color: #fff;
}

.main__text .faq--title {
  text-align: left;
}

.about__content {
  background-color: var(--color-burnt-browm);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.main__header .about__h2 {
  margin-top: 2rem;
}

.footer {
  background-color: var(--color-polish-brown);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.social__media {
  justify-content: center;
  align-items: center;
  display: flex;
}

.media {
  width: 1.8rem;
  height: 1.8rem;
  color: var(--color-dark-brown);
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
  display: flex;
}

.footer p {
  color: #fff;
  text-align: center;
  margin-top: 16px;
  font-size: .8rem;
}

.about {
  flex-direction: column;
  gap: 4rem;
  display: flex;
}

.about__header {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  display: flex;
}

.about__title {
  text-align: center;
  color: var(--color-dark-brown);
  font-size: 2.4rem;
}

.main__about--section {
  grid-gap: 1rem;
  padding: .8rem;
  display: grid;
}

.main__about--card {
  border-radius: .8rem;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: auto;
  height: auto;
  padding: 1.6rem;
  display: flex;
}

.main__about--card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #000c;
}

.main__about--card img {
  width: 100px;
}

.about__page--text {
  text-align: center;
  line-height: 2;
}

.card--1, .card--3, .card--5, .card--7, .card--9 {
  background-color: var(--color-golden-yellow);
  color: #fff;
}

.main__about--card h2 {
  text-align: center;
  font-size: 2rem;
}

.main__about--card p {
  text-align: left;
  font-size: 1rem;
  line-height: 2;
}

.main__about--card ul li {
  font-size: 16px;
  line-height: 2;
  list-style: outside;
}

.service__heading, .service__title {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.service__title h2 {
  text-align: center;
  color: var(--color-dark-brown);
  font-size: 2.4rem;
}

.service__heading p {
  text-align: center;
  padding: 1.4rem;
  font-size: 1rem;
  line-height: 2;
}

.main__service--section {
  grid-gap: 16px;
  padding: 8px;
  display: grid;
}

.service__section {
  flex-direction: column;
  align-items: center;
  padding: 1.4rem;
  line-height: 2;
  display: flex;
}

.service__section .service__h2 {
  color: #121212;
  text-align: center;
  font-size: 2rem;
}

.service__section p, .service__section ul {
  color: #121212;
  text-align: left;
  font-size: 1rem;
}

.service__section ul li {
  list-style: outside;
}

.service__section img {
  width: 100px;
}

.faq__title {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4rem;
  display: flex;
}

.faq__title h2 {
  text-align: center;
  color: var(--color-dark-brown);
  font-size: 2.4rem;
}

.faq__content {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: .8rem;
  display: flex;
}

.faq__question {
  background-color: var(--color-polish-brown);
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 1.4rem;
  display: flex;
}

.faq__btn, .faq__icon {
  color: #fff;
  font-size: 1rem;
  font-weight: bold;
}

.faq__icon {
  cursor: pointer;
}

.faq__answer {
  background-color: var(--color-burnt-browm);
  border-radius: 4px;
  width: 100%;
  height: auto;
  padding: 1.4rem;
  display: none;
}

.faq__text {
  color: #fff;
  text-align: left;
  font-size: 1rem;
}

.faq__answer.active {
  display: block;
}

.contact {
  flex-direction: column;
  gap: 4rem;
  display: flex;
}

.contact__headings {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.contact__headings h2 {
  text-align: center;
  color: var(--color-dark-brown);
  font-size: 2.4rem;
}

.contact__subsection {
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 1.4rem;
  display: flex;
}

.contact__content {
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  height: auto;
  padding: 16px;
  display: flex;
}

.contact__content:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #000c;
}

.contact__content .contact__img {
  width: 70px;
}

.contact__one {
  background-color: var(--color-burnt-browm);
}

.contact__two {
  color: #fff;
}

.contact__text {
  align-items: left;
  flex-direction: column;
  display: flex;
}

.contact__text h2 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.8rem;
  line-height: 2;
}

.contact__text p {
  text-align: left;
  font-size: 1rem;
  line-height: 2;
}

.contact__text ul li {
  font-size: 1rem;
  line-height: 2;
}

.contact__form--content {
  flex-direction: column;
  align-items: center;
  gap: 4rem;
  padding: 1.4rem;
  display: flex;
}

.cont__main--title, .contact__text .cont__text--title {
  text-align: center;
  font-size: 1.8rem;
}

.contact__text .cont__text--title {
  text-align: left;
  font-size: 1.8rem;
}

.contact__content--address {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 2;
  display: flex;
}

.contact__content--address p {
  text-align: center;
  font-size: 1rem;
}

form {
  background-color: var(--color-burnt-browm);
  border-radius: .8rem;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  margin-top: 1rem;
  padding: 1rem;
  display: flex;
}

.input__group {
  color: #fff;
  flex-direction: column;
  width: 100%;
  display: flex;
}

textarea, input {
  resize: none;
  width: 100%;
}

input {
  margin-bottom: 16px;
  padding: 8px;
}

@media (width >= 640px) {
  .hero h1 {
    font-size: 3.4rem;
  }

  form {
    width: 800px;
  }
}

@media (width >= 768px) {
  .service__section {
    width: 100%;
    display: block;
  }

  .input__wrap {
    gap: 16px;
    display: flex;
  }

  .input__wrap .input-group {
    margin-right: 2rem;
  }

  .input__wrap .input-group:last-child {
    margin-right: 0;
  }

  .about__text {
    background-color: var(--color-burnt-browm);
  }

  .main__text .service__p, .main__text .faq--title {
    color: #121212;
  }

  .main__text, .main__header, .service__title, .service__heading p {
    flex: 50%;
  }

  .main__text p, .main__text h3 {
    color: #fff;
  }

  .main__about--card h2 {
    font-size: 32px;
  }

  .main__header .main__title, .service__title h2 {
    font-size: 4.4rem;
  }

  .service__section h2 {
    text-align: left;
    font-size: 1.8rem;
  }

  .service__section .service__h2 {
    font-size: 2rem;
  }

  .service__section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2.4rem;
    display: flex;
  }

  .faq__answer, .faq__question {
    width: 50%;
  }

  .about__title, .faq__title h2, .contact__headings h2 {
    font-size: 3.4rem;
  }

  .line {
    width: 300px;
  }

  .contact__subsection, .contact__form--content {
    flex-direction: row;
  }
}

@media (width >= 1024px) {
  .logo img {
    width: 100px;
  }

  .menu {
    display: none;
  }

  .list {
    background-color: inherit;
    visibility: visible;
    opacity: 1;
    position: inherit;
    flex-direction: row;
    width: 500px;
  }

  .list-item {
    border-radius: 5px;
    margin: 0 16px;
    padding: 4px;
  }

  .list-link {
    color: #fff;
    width: inherit;
    border: none;
    padding: 0;
  }

  .main__service--section {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, fr);
  }

  .span-two {
    grid-column: span 2;
  }

  .contact__content--address .address__title, .service__main--title, .about__page--text {
    max-width: 800px;
  }

  .about, .contact, .contact__subsection, .contact__form--content {
    gap: 8rem;
  }
}

@media (width >= 1200px) {
  .main__about--section, .main__service--section {
    grid-template-columns: repeat(3, 1fr);
  }

  .card--2, .card--3, .card--6, .card--7 {
    background-color: inherit;
    color: inherit;
  }

  .card--1, .card--3, .card--5, .card--7, .card--9 {
    background-color: var(--color-golden-yellow);
    color: #fff;
  }
}
